import Vue from 'vue'
import VueRouter from 'vue-router'
import Desktop from '../views/Desktop.vue'

Vue.use(VueRouter)

  const routes = [
  {
    path: '/',
    name: 'Desktop',
    component: Desktop
  },
  {
    path: '/about',
    name: 'About',
    component: () => import(/* webpackChunkName: "about" */ '../views/About.vue')
  }
]

const router = new VueRouter({
  routes
})

export default router
